/*
 * WordPressのログイン画面
 */

// MIXIN グラデーション
@import "module/gradation";


$color-primary: #3489ca;
$bg-color: #f9f9f9;


body.login {
	background-color: $bg-color;
	@include sgradient(
		$type:radial,
		$from:top right,
		$colors:(
			(darken($bg-color, 2%), 0%),
			(lighten($bg-color, 2%), 50%),
			(darken($bg-color, 2%), 100%)
		)
	);
	div#login {
		form#loginform {
			padding-bottom: 26px;
			background-color: rgba(#fff, .6);
			border-radius: 0;
			p {
				&.forgetmenot {
					float: none;
					input#rememberme {
						&:checked {
							&:before {
								color: $color-primary;
							}
						}
					}
				}
				&.submit {
					margin-top: 20px;
					text-align: center;
					input#wp-submit {
						float: none;
						width: 80%;
						background: $color-primary;
						border-color: darken($color-primary, 5%);
						box-shadow: 0 1px 0 $color-primary;
						text-shadow: 0 -1px 1px $color-primary, 1px 0 1px $color-primary, 0 1px 1px $color-primary, -1px 0 1px $color-primary;
					}
				}
			}
			input {
				&#user_login {

				}
				&#user_pass {

				}
				&#user_login,
				&#user_pass {
					background-color: rgba(#fff, .2);

				}
				&:focus {
					border-color: $color-primary;
					box-shadow: 0 0 2px rgba($color-primary, .8);
				}
			}
		} // form#loginform
		p#nav {
			display: none;
			a {

			}
		}
		p#backtoblog {
			display: none;
			a {

			}
		}
	} // div#login
} // body.login


body.login div#login h1 a {
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    display: block;
    transition: none;
}

@media (max-width: 640px) {
	body.login div#login h1 a {
	    width: 320px;
	    height: 55px;
	    background-size: 100%;
	    background-position: -5px -70px;
	}
}

@media (min-width: 641px) {
	body.login div#login h1 a {
	    width: 320px;
	    height: 55px;
	    background-size: 200%;
	    background-position: -5px -5px;
	}
}

