@charset "UTF-8";
/*
 * WordPressのログイン画面
 */
body.login {
  background-color: #f9f9f9;
  background-color: #f4f4f4;
  background: -webkit-radial-gradient(top right, ellipse cover, #f4f4f4 0%, #fefefe 50%, #f4f4f4 100%);
  background: -moz-radial-gradient(top right, ellipse cover, #f4f4f4 0%, #fefefe 50%, #f4f4f4 100%);
  background: -ms-radial-gradient(top right, ellipse cover, #f4f4f4 0%, #fefefe 50%, #f4f4f4 100%);
  background: -o-radial-gradient(top right, ellipse cover, #f4f4f4 0%, #fefefe 50%, #f4f4f4 100%);
  background: radial-gradient(ellipse at top right, #f4f4f4 0%, #fefefe 50%, #f4f4f4 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(GradientType=1, StartColorStr='#f4f4f4', EndColorStr='#f4f4f4'); }
  body.login div#login form#loginform {
    padding-bottom: 26px;
    background-color: rgba(255, 255, 255, 0.6);
    border-radius: 0; }
    body.login div#login form#loginform p.forgetmenot {
      float: none; }
      body.login div#login form#loginform p.forgetmenot input#rememberme:checked:before {
        color: #3489ca; }
    body.login div#login form#loginform p.submit {
      margin-top: 20px;
      text-align: center; }
      body.login div#login form#loginform p.submit input#wp-submit {
        float: none;
        width: 80%;
        background: #3489ca;
        border-color: #2f7bb6;
        box-shadow: 0 1px 0 #3489ca;
        text-shadow: 0 -1px 1px #3489ca, 1px 0 1px #3489ca, 0 1px 1px #3489ca, -1px 0 1px #3489ca; }
    body.login div#login form#loginform input#user_login, body.login div#login form#loginform input#user_pass {
      background-color: rgba(255, 255, 255, 0.2); }
    body.login div#login form#loginform input:focus {
      border-color: #3489ca;
      box-shadow: 0 0 2px rgba(52, 137, 202, 0.8); }
  body.login div#login p#nav {
    display: none; }
  body.login div#login p#backtoblog {
    display: none; }

body.login div#login h1 a {
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  display: block;
  transition: none; }

@media (max-width: 640px) {
  body.login div#login h1 a {
    width: 320px;
    height: 55px;
    background-size: 100%;
    background-position: -5px -70px; } }

@media (min-width: 641px) {
  body.login div#login h1 a {
    width: 320px;
    height: 55px;
    background-size: 200%;
    background-position: -5px -5px; } }
